import Vue from "vue";
import VueRouter from "vue-router";
import Login from "comps/Login.vue";
import NotebookList from "comps/NotebookList.vue";
import NoteDetail from "comps/NoteDetail.vue";
import TrashDetail from "comps/TrashDetail.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Login",
    component: Login
  },
  {
    path: "/notebooks",
    component: NotebookList
  },
  {
    path: "/note/:noteId",
    component: NoteDetail
  },
  {
    path: "/trash/:noteId",
    component: TrashDetail
  }
];

const router = new VueRouter({
  routes
});

export default router;
