<template>
  <div id="login">
    <div class="modal-mask">
      <div class="modal-wrapper">
        <div class="modal-container">
          <div class="main"></div>
          <div class="form">
            <h3 @click="showRegister">创建账户</h3>
            <transition name="slide" mode="in-out">
              <div v-show="isShowRegister" class="register">
                <input
                  type="text"
                  placeholder="用户名"
                  v-model="register.username"
                />
                <input
                  type="password"
                  placeholder="密码"
                  v-model="register.password"
                />
                <p v-bind:class="{ error: register.isError }">
                  {{ register.notice }}
                </p>
                <div class="button" @click="onRegister">创建账号</div>
              </div>
            </transition>

            <h3 @click="showLogin">登录</h3>
            <transition name="slide" mode="in-out">
              <div v-show="isShowLogin" class="login">
                <input
                  type="text"
                  placeholder="输入用户名"
                  v-model="login.username"
                />
                <input
                  type="password"
                  placeholder="密码"
                  v-model="login.password"
                />
                <p v-bind:class="{ error: login.isError }">
                  {{ login.notice }}
                </p>
                <div class="button" @click="onLogin">登录</div>
              </div>
            </transition>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Auth from "@/apis/auth";
import Bus from "helpers/bus";

export default {
  name: "Login",
  data() {
    return {
      isShowLogin: true,
      isShowRegister: false,
      login: {
        username: "",
        password: "",
        notice: "请输入用户名和密码",
        isError: false
      },
      register: {
        username: "",
        password: "",
        notice: "创建后请记住账号密码",
        isError: false
      }
    };
  },
  beforeCreate() {
    Auth.getInfo().then((res) => {
      if (res.isLogin) {
        this.$router.push("/notebooks");
      }
    });
  },
  methods: {
    showRegister() {
      this.isShowLogin = false;
      this.isShowRegister = true;
    },
    showLogin() {
      this.isShowLogin = true;
      this.isShowRegister = false;
    },

    // 按钮事件
    onRegister() {
      let { isValid: nameValid, notice: nameNotice } = this.validUserName(
        this.register.username
      );
      let { isValid: pwdValid, notice: pwdNotice } = this.validPassword(
        this.register.password
      );

      if (!nameValid || !pwdValid) {
        this.register.isError = true;
        this.register.notice = nameNotice || pwdNotice;
        return;
      }
      this.register.isError = false;
      this.register.notice = "";

      Auth.register({
        username: this.register.username,
        password: this.register.password
      }).then(
        () => {
          console.log("注册成功");
          this.isShowLogin = true;
          this.isShowRegister = false;
        },
        () => {
          console.log("注册失败");
        }
      );
    },
    onLogin() {
      let { isValid: nameValid, notice: nameNotice } = this.validUserName(
        this.login.username
      );
      let { isValid: pwdValid, notice: pwdNotice } = this.validPassword(
        this.login.password
      );

      if (!nameValid || !pwdValid) {
        this.login.isError = true;
        this.login.notice = nameNotice || pwdNotice;
        return;
      }
      this.login.isError = false;
      this.login.notice = "";

      Auth.login({
        username: this.login.username,
        password: this.login.password
      }).then(
        () => {
          console.log("登陆成功");
          Bus.$emit("getInfo", this.login.username);
          this.$router.push("/notebooks");
        },
        () => {
          console.log("登陆失败");
        }
      );
    },

    // 校验
    validUserName(username) {
      let isValid = /^[\w\d_u4e00-\u9fa5]{3,15}$/.test(username);
      return {
        isValid,
        notice: isValid
          ? ""
          : "用户名必须是3~15个字符，仅限于字母数字下划线以及中文"
      };
    },
    validPassword(password) {
      let isValid = /^.{6,16}$/.test(password);
      return {
        isValid,
        notice: isValid ? "" : "密码长度必须为6~16个字符"
      };
    }
  }
};
</script>

<style lang="less">
.modal-mask {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: table;
  transition: opacity 0.3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 80vw;
  max-width: 800px;
  height: 50vh;
  max-height: 500px;
  margin: 0px auto;
  background-color: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  transition: all 0.3s ease;
  font-family: Helvetica, Arial, sans-serif;
  display: flex;
  border-radius: 5px;

  .main {
    flex: 1;
    background: #36bc64
      url(//cloud.hunger-valley.com/17-12-13/38476998.jpg-middle) center center
      no-repeat;
    background-size: contain;
    border-radius: 5px 0 0 5px;
  }
  .form {
    width: 270px;
    border-left: 1px solid #ccc;
    overflow: hidden;

    h3 {
      border-radius: 0 5px 0 0;
      padding: 10px 20px;
      font-weight: normal;
      font-size: 16px;
      border-top: 1px solid #eee;
      cursor: pointer;

      &:nth-of-type(2) {
        border-bottom: 1px solid #eee;
      }
    }

    .button {
      background-color: #2bb964;
      height: 36px;
      line-height: 36px;
      text-align: center;
      font-weight: bold;
      color: #fff;
      border-radius: 4px;
      margin-top: 18px;
      cursor: pointer;
    }

    .login,
    .register {
      background: white;
      padding: 0px 20px;
      border-top: 1px solid #eee;

      input {
        display: block;
        width: 100%;
        height: 35px;
        line-height: 35px;
        padding: 0 6px;
        border-radius: 4px;
        border: 1px solid #ccc;
        outline: none;
        font-size: 14px;
        margin-top: 10px;
      }
      input:focus {
        border: 3px solid #9dcaf8;
      }

      p {
        font-size: 12px;
        margin-top: 10px;
        color: #444;
      }
      .error {
        color: red;
      }
    }
    .login {
      border-top: 0;
      overflow: hidden;
    }
    .register {
      overflow: hidden;
      height: 200px;
    }
    .slide-enter-active,
    .slide-leave-active {
      transition: all 0.8s;
    }
    .slide-enter-to,
    .slide-leave {
      height: 200px;
      border-bottom: 0;
      overflow: visible;
    }
    .slide-enter,
    .slide-leave-to {
      height: 0px;

      opacity: 0;
    }
  }
}
</style>
